export class EmittersPathShapeOptions {
    constructor() {
        this.points = [];
    }
    load(data) {
        if (!data) {
            return;
        }
        if (data.points !== undefined) {
            this.points = data.points.map((t) => ({ x: t.x ?? 50, y: t.y ?? 50 }));
        }
    }
}
